* {
    box-sizing: border-box;
}

body {
    margin: 0;
    padding: 0;
}

section#score {
    width: 100%;
}

#board {
    width: 640px;
    height: 640px;
    margin: 1em auto;
}

#board  > div {
    border: 1px solid black;
    float: left;
    width: 64px;
    height: 64px;
}

section#score div {
    width: 10em;
    height: 3em;
    text-align: center;
    padding: 0.5em;
    background-color: rgba(211,211,211, 0.75);
    border: 1px solid lightgray;
    border-radius: 1px;
    box-shadow: 1px 1px 5px 1px lightgray;
    margin: 1em auto;
}

.furry {
    background-image: url('../images/furry.png');
    background-repeat: no-repeat;
    background-size: cover;
}

.coin {
    background-image: url('../images/coin.png');
    background-repeat: no-repeat;
    background-size: cover;
}

section#start div {
    width: 10em;
    height: 2.2em;
    text-align: center;
    padding: 0.5em;
    background-color: rgba(160,255,160, 0.75);
    border: 1px solid lightgray;
    border-radius: 1px;
    box-shadow: 1px 1px 5px 1px lightgray;
    margin: 1em auto;
    cursor: pointer;
}

section#start div:hover {
    background-color: rgba(185,184,255, 0.75);
    box-shadow: 0 0 5px 0 lightgray;
}

section#over {
    position: fixed;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 1;
    padding: 10px;
    background-color: rgba(255, 255, 0, 0.75);
}

section#over pre {
    display: inline-block;
    width: 100%;
    text-align: center;
    font-size: 10px;
    font-weight: bolder;
    color: brown;
}

.invisible {
    display: none;
}